import firebaseObj from "../firebase.js";
import { getAuth } from "firebase/auth";
import { collection, addDoc, serverTimestamp } from "firebase/firestore";
export default {
  name: "Home",
  data: () => ({
    user: null,
    fund: { amount: "", purpose: "" },
    expense: "",
    snackbar: false,
    text: `Hello, I'm a snackbar`,
  }),
  components: {},
  mounted() {},

  methods: {
    async addTransaction(type) {
      let data = {
        type: type,
        amount: this.fund.amount,
        user: this.user.uid,
        username: this.user.displayName,
        date: serverTimestamp(),
      };
      if (type == "expense") {
        data["purpose"] = this.fund.purpose;
        data["amount"] = this.expense;
      }
      let docRef = await addDoc(
        collection(firebaseObj.firestore, "transaction"),
        data
      );
      this.snackbar = true;
    },
  },
  beforeMount() {
    const auth = getAuth();

    auth.onAuthStateChanged((user) => {
      if (user) {
        console.log(user);
        this.user = user;
      } else {
        console.log("Not Authenticated");
        this.user = null;
      }
    });
  },
};
